html, body{
    overflow-x: hidden;
}

/* Fonts  */
@font-face{
    font-family: yugbsm;
    src: url(./fonts/KGWhYYouGoTtABeSoMeAn.ttf)
}

.yugbsm{
    font-family: yugbsm;
}

@font-face{
    font-family: outbox;
    src: url(./fonts/TheOutboxSt.ttf)
}

.outbox{
    font-family: outbox;
}

/* Bootstrap */
/*.container-fluid{
    padding-left: 0!important;
    padding-right: 0!important;
    margin-right: auto!important;
    margin-left: auto!important;
}*/

/* Nav */
#navbar{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: #3aafa9!important;
}

nav ul li a{
    color: white;
    text-decoration: none;
    font-family: outbox;
}

nav ul li a:hover{
    color: rgb(209, 209, 209);
    text-decoration: none;
}

#navbar button{
    background-color: #17242a;
    color: white!important;
}

.navbar-toggler-icon{
    color: white!important;
}

/* Landing Content */
.landing-content{
    background-image: url(imgs/landing.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
    z-index: -100;
    position: relative;
}

/* About */
#about{
    background-color: red; /*#17252a;*/
    color: white;
}

#about h1{
    padding: 3rem;
    font-size: 8rem;
}

#about h2{
    font-size: 5rem;    
    padding-top: 2rem;
}

/* Podcasts */
#podcasts div h1{
    padding: 3rem;
    font-size: 8rem;
}

.guest-name{
    font-weight: bold;
    padding-bottom: 0;
    margin-bottom: 0;
}

.icon{
    padding: .5rem;
}

.fa-youtube{
    color: red;
}

.fa-youtube:hover{
    color: rgb(253, 84, 84);
}

.fa-twitter{
    color: #1da1f2;
}

.fa-twitter:hover{
    color: #1f81be;
}

.fa-soundcloud{
    color: #ff6b00;
}

.fa-soundcloud:hover{
    color: #ff9c56;
}

.fa-facebook{
    color: #3b589c;
}

.fa-facebook:hover{
    color: #324d83;
}

.fa-instagram{
    color: #e02c76;
}

.fa-instagram:hover{
    color: #b8205f;
}

#download{
    color: rgb(65, 67, 68);

}

#download:hover{
    color: rgb(143, 151, 155);

}

/* Contact */
#contact{
    background-color: #1f2833;
    color: white;
}

/* Footer */
#footer{
    background-color: #1f2833;
    color: white;
}

.hr-style{
    padding: 0;
    margin: 0;
    border-top: 1px solid white;
}

/* Extra small devices (portrait phones, less than 576px)
// No media query for `xs` since this is the default in Bootstrap */

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 577px) { 

}
/* Medium devices (tablets, 768px and up) */
@media (max-width: 769px) { 
    /*About*/
    .small-bp{
        padding-bottom: 3rem;
    }

    #about h1{
        padding: 3rem;
        font-size: 5rem;
    }

    #about h2{
        font-size: 3rem;    
        padding-top: 2rem;
    }

    /*Podcasts*/
    #podcasts div h1{
        padding: 3rem;
        font-size: 5rem;
    }

    .col-12-wrap{
        text-align: center;
    }
 }

/* Large devices (desktops, 992px and up) */
@media (max-width: 993px) { 
    .col-12-wrap{
        text-align: center;
    }
 }

